import React, { useEffect, useState } from 'react'
import { Slider } from './Slider'
import MobileStepper from '@material-ui/core/MobileStepper';

export const Testimonials = ({ src }) => {

    const [state, setState] = useState(0);

    useEffect(() => {

        const option = document.querySelectorAll('.MuiMobileStepper-dot');

        option.forEach((element, key) => {
            element.addEventListener('click', () => setState(key))
        });

    }, [])

    return (
        <div className="Testimonials">

            <Slider src={src[state]} />

            <MobileStepper
                steps={src.length}
                position="static"
                variant="dots"
                activeStep={state}
                className="Items-Slider"

            />
        </div>
    )
}
